<template>
  <div class="row">
    <div class="col-md-6 col-md-offset-3">
      <div class="panel panel-default">
        <div class="panel-body">
          <div id="wrap-card">
            <!--<button class="btn btn-success" @click="send($event)">Buscar</button>-->
            <input :value="input" @focusout="send($event)" id="username" v-model="username" class="inverted" type="text" name="user" value="" placeholder="Digite um login Github">
            <div class="avatar-wrap">
              <div class="avatar">
                <img v-bind:src="resultado.avatar_url">
              </div>
            </div>
            <div class="user-wrap">
              <div class="username">

                <a v-if="resultado" href="#">{{resultado.login}}</a>
                <a v-else href="#">Login</a>

              </div>
              <div class="quote">
                <i>{{resultado.bio}}</i>
              </div>
            </div>
            <div class="content-wrap">
              <ul>
                <li>
                  <i>{{resultado.public_repos}}</i>
                  <span>Repos</span>
                </li>
                <li>
                  <i>{{resultado.followers}}</i>
                  <span>Followers</span>
                </li>
                <li>
                  <i>{{resultado.following}}</i>
                  <span>Following</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
	
        username: "rodrigomoraes",
        //resultado: null,
        msg: "apenas um show",
        erro: "false",
        resultado: {}
      }
    },
    methods:{
      send: function(e){
        //e.preventDefault();
        this.$http.get('https://api.github.com/users/' + this.username)
        .then(function(result){
          this.resultado = result.data;
          this.erro = false;
          if(this.resultado.login){
            //this.usrnames.push(self.resultado);
          }
        })
       .catch(function(err){
          this.erro = true;
          this.resultado = {};
        })
        .finally(function(){

          this.username = '';

        })

      }
    }


  }


</script>

<style media="screen">
.panel{
  border: none;
}
.user-wrap{
  text-align: center;
  margin-bottom: 15px;
}

.username a{
  font-size: 25px;
}
  #wrap-card{
    border: 1px solid #ddd;
  }
  #wrap-card input{
    padding: 5px;
    background-color: #24292e;
    border: none;
    width: 100%;
    color: #ccc;


  }
  .avatar{
    width: 200px;
    height: 200px;
    margin: 0 auto;
    margin-bottom: 20px;
    margin-top: 20px;
  }
  .avatar img{
    width: 200px;
    height: auto;
    border-radius: 15%;
    margin: 0;
  }
  .content-wrap{
    padding: 20px;
    background-color: #24292e;
    color: #fff;
  }
  .content-wrap ul{
    list-style: none;
    padding: 0;
  }
  .content-wrap ul li{
    display: inline-block;
    width: 30%;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
  }
  .content-wrap ul li i{
    display: block;
  }
</style>



